.Usuarios {
  width: 100%;
  height: 100%;
  background-color: var(--bgColor);
  display: flex;
}

.Usuarios .content {
  margin: 40px;
  width: calc(100% - 80px);
  flex: 1;
  display: flex;
  flex-direction: column;
  overflow: hidden;
}

.Usuarios .content > .row {
  display: flex;
  height: 100%;
  max-height: 100%;
  width: 100%;
  overflow: hidden;
  align-items: flex-start;
  justify-content: flex-start;
  margin-top: 20px;
  position: relative;
}

.Usuarios .content > .row.relatorios {
  width: 100%;
  justify-content: center;
}

@media screen and (max-width: 1254px) {
  .Usuarios {
    height: calc(100% - 52px);
    position: fixed;
    top: 0;
    width: 200%;
  }

  .Usuarios .content {
    margin: 20px;
    width: calc(100% - 40px);
  }

  .Usuarios .content > .row.relatorios {
    width: calc(100vw - 40px);
    justify-content: center;
  }

  .Usuarios .content > .row.relatorios .FormUser {
    width: 100%;
  }
}
