/*TEXTFIELD --------------------------------*/
.MuiInputBase-root {
  background-color: white !important;
  border-radius: 4px !important;
  transition: all ease 0.2s !important;
}

.secondary .MuiInputBase-root {
  background-color: var(--bgColor) !important;
}

.MuiInputLabel-root {
  margin-left: 8px;
}

.MuiFormControl-root:not(.DmTextField):not(.iconified) .MuiInputBase-input {
  padding-left: 0 !important;
}

.MuiFormControl-root.DmTextField .MuiInputBase-input {
  padding-left: 20px !important;
}

.iconified .MuiInputLabel-root {
  margin-left: 32px;
}

.iconified .MuiInputBase-input {
  padding-left: 45px !important;
}

.MuiInputLabel-root:not(.Mui-focused) {
  color: #aaa;
  font-size: 16px;
  transition: all ease 0.2s;
}

.MuiInputBase-root.Mui-focused {
  box-shadow: inset 0 0 2px 1px var(--primary50);
}

.MuiInputBase-root.Mui-error {
  box-shadow: inset 0 0 2px 1px var(--errorColor);
}

/*HELPERTEXT --------------------------------*/
.MuiFormHelperText-root {
  margin: 0 !important;
  font-size: 0.7rem !important;
  font-style: italic !important;
}

/*BUTTON --------------------------------*/
.MuiButton-root {
  background-color: var(--primaryColor) !important;
  font-size: 16px !important;
  font-weight: var(--extraBold) !important;
  border-radius: 4px !important;
  height: 50px;
  box-shadow: none !important;
  color: white;
  transition: all ease 0.3s !important;
}

.MuiDialog-container > div.MuiDialog-paperWidthSm .MuiButton-root {
  background-color: unset !important;
  font-size: unset !important;
  font-weight: 600 !important;
  border-radius: inherit !important;
  height: unset;
  box-shadow: unset !important;
  font-family: unset !important;
  transition: all ease 0.3s !important;
}

:not(.MuiDialog-paperWidthSm) .MuiBox-root .MuiButton-root.Mui-disabled {
  color: white !important;
}

:not(.MuiDialog-paperWidthSm) .MuiButton-root.errorColor {
  background-color: var(--errorColor) !important;
}

:not(.MuiDialog-paperWidthSm) .MuiButton-root.errorSecondary {
  background-color: var(--error50) !important;
}
:not(.MuiDialog-paperWidthSm) .MuiButton-root.secondary {
  background-color: var(--primary50) !important;
}

.filter .MuiButton-root.default {
  background-color: unset !important;
  color: var(--primary50) !important;
  font-weight: var(--medium) !important;
  height: 32px;
  transition: all ease 0.3s;
}

:not(.MuiDialog-paperWidthSm) .MuiButton-root.success {
  background-color: #4bb543 !important;
}

:not(.MuiDialog-paperWidthSm) .MuiButton-root.tertiary {
  background-color: transparent !important;
  color: #aaa;
  font-weight: var(--regular) !important;
  border: 2px dashed #aaa;
  height: 32px;
  transition: all ease 0.3s;
}

:not(.MuiDialog-paperWidthSm) .MuiButton-root.tertiary.Mui-focusVisible:not(.active) {
  border-color: var(--primary50) !important;
  color: var(--primary50) !important;
}

:not(.MuiDialog-paperWidthSm) .MuiButton-root.tertiary.active {
  background-color: var(--primaryColor) !important;
  color: white !important;
  border: none;
}

.filterButton {
  transition: all ease 0.2s;
}

.form-buttons {
  display: grid;
  grid-template-columns: 1fr 1fr;
  column-gap: 20px;
  margin-top: 20px;
  transition: all ease 0.2px;
}

/*CHECKBOX --------------------------------*/
.App .MuiFormControlLabel-root .MuiTypography-root,
.filter .MuiTypography-root.MuiTypography-h5 {
  transition: all ease 0.2s;
  color: #aaa;
}

.MuiFormControlLabel-root .Mui-checked ~ .MuiTypography-root {
  color: var(--primaryColor);
}

.MuiFormControlLabel-root .MuiCheckbox-root .MuiSvgIcon-root {
  color: #aaa;
}

.MuiFormControlLabel-root .MuiCheckbox-root.Mui-checked .MuiSvgIcon-root {
  color: var(--primaryColor);
}

/*TYPOGRAPHY --------------------------------*/
.MuiTypography-root.MuiTypography-subtitle1 {
  font-size: 12px;
  color: #aaa;
}

.App .MuiTypography-root.MuiTypography-h5,
.filter .MuiTypography-root.MuiTypography-h5 {
  font-weight: var(--extraBold);
  margin-bottom: 10px;
  color: #272727;
}

.MuiTypography-root.MuiTypography-h5.primary,
.filter .MuiTypography-root.MuiTypography-h5 {
  font-weight: var(--extraBold);
  margin-bottom: 10px;
  color: var(--primaryColor);
}

.App .MuiTypography-root.MuiTypography-h6,
.filter .MuiTypography-root.MuiTypography-h5 {
  font-weight: var(--extraBold);
  margin-bottom: 10px;
  color: #272727;
}

/*DRAWER ---------------------------------------*/
.MuiDrawer-paper {
  margin-left: 58px;
  display: flex;
  justify-content: center;
  background-color: #e5e5ef !important;
}

.MuiDrawer-paper .MuiListItemText-root {
  color: #555;
}

.MuiDrawer-paper a {
  text-decoration: none;
  color: var(--primary50);
}

.MuiDrawer-paper .MuiListItemText-root.active {
  color: var(--primaryColor);
}

/*TABS -----------------------------------------*/

.MuiTabs-root {
  width: 350px;
  margin-bottom: 16px;
  height: 48px !important;
}

.MuiTabs-root .MuiButtonBase-root {
  width: 175px;
  color: #aaa;
  border-bottom: 2px solid #ddd;
  font-size: 12pt;
  font-weight: var(--regular);
  padding-left: 0;
  padding-right: 0;
}

.MuiTabs-root .MuiButtonBase-root.Mui-selected {
  color: var(--primaryColor);
}

.MuiTabs-root .MuiTabs-indicator {
  transition: all ease 0.4s;
}

/*AUTOCOMPLETE ------------------------------*/

.MuiAutocomplete-root.noTabIndexable input {
  opacity: 0;
}

.MuiAutocomplete-root {
  opacity: 1;
  transition: opacity ease 0.3;
}

.MuiAutocomplete-root + .MuiAutocomplete-popper {
  opacity: 1;
  transition: opacity ease 0.3;
}

.MuiAutocomplete-root.displayNone {
  opacity: 0;
}

.MuiAutocomplete-root.displayNone + .MuiAutocomplete-popper {
  opacity: 0 !important;
}

/*DATEPICKER ---------------------------------*/

.MuiCalendarPicker-root {
  width: 310px !important;
}

.MuiMonthPicker-root {
  width: inherit !important;

  padding: 18px;
}

.MuiMonthPicker-root .PrivatePickersMonth-root {
  margin: 5px;
  height: 30px;
  width: 50px !important;
  color: black;
  font-size: 14px;
  text-transform: uppercase;
  background-color: white;
  border: none;
  box-shadow: 2px 2px 5px #d3d3d3, -3px -3px 6px #ffffff;
  border-radius: 8px;
  transition: all ease 0.3s;
}

.MuiMonthPicker-root .PrivatePickersMonth-root:hover {
  background-color: #eee;
}

/*CARD -----------------------------------------------*/

.MuiCard-root {
  flex: 1;
  padding: 15px;
  width: 170px;
  border-radius: 20px !important;
  display: flex;
}

.MuiCard-root .MuiCardContent-root {
  padding: 0 !important;
  flex: 1;
  display: flex;
  flex-direction: column;
}

.MuiCard-root .MuiCardContent-root p.date {
  font-size: 10px;
  line-height: 10px;
  color: #aaa;
  margin: 0;
  margin-top: 10px;
}

.MuiCard-root .MuiCardContent-root h2.nameReg {
  margin: 0;
  margin-top: 10px;
  color: #272727;
  font-size: 14px;
  word-wrap: normal;
  flex: 1;
}

/*BACKDROP --------------------------*/

.MuiBackdrop-root {
  background-color: rgba(0, 0, 0, 0.3) !important;
  backdrop-filter: blur(6px) !important;
}

/*MODAL -------------------------------*/

.MuiBox-root.modalBox-root {
  position: fixed;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 750px;
  max-height: calc(90% - 80px);
  padding: 40px;
  background-color: white;
  border-radius: 16px;
  display: flex;
  flex-direction: column;
  align-items: center;
  box-shadow: 10px 10px 40px rgba(0, 0, 0, 0.4);
  outline: none;
  transition: all ease 0.2s;
}

/*RANGEPICKER -----------------------------*/

@media screen and (max-width: 1254px) {
  .MuiBox-root.modalBox-root {
    position: fixed;
    width: calc(100% - 40px);
    padding: 50px 20px;
    height: calc(100% - 100px);
    max-height: calc(100% - 100px);
    border-radius: 0;
  }
}

@media screen and (min-width: 1255px) {
  :not(.MuiDialog-paperWidthSm) .MuiButton-root.tertiary:hover:not(.Mui-focusVisible):not(.active) {
    border-color: var(--primary50) !important;
    color: var(--primary50) !important;
  }
  .filterButton:hover {
    color: var(--primaryColor);
    background-color: var(--primary50);
  }
}
